var data = [
    {
        id: 1,
        name: "example data 1"
    },
    {
        id: 2,
        name: "example data 2"
    }
];
// --------------------------------------------------------------------
export function GET(req, res) {
    res.send(data);
}
// --------------------------------------------------------------------
export function POST(req, res) {
    data.push(req.body);
    res.send({
        message: "Your data has been submited."
    });
}
